//@fileoverview Component that generates the applications contact form section

import {React, useState} from 'react';
//Import formik library to validate contact form
import { useFormik } from 'formik';
//Import Alert library to show confirmation message after form validation is correct
import Alert from 'react-bootstrap/Alert';

//Function that creates and returns the applications contact form section
//@return Contact form UI in html format
const Contact = () => {

    //Create state variables
    const [validated, setValidated] = useState(false);
    const [showConf, setShowConf] = useState(false);

    //Function to validate form, it validates type of data
    const validate = values => {
        const errors = {};
        
        if (!values.txtNombre) {
            errors.txtNombre = 'Por favor, ingresa un nombre';
        } else if(!/^[A-Za-z]+$/.test(values.txtNombre)){
            errors.txtNombre = 'El nombre no debe contener números';
        }else if (values.txtNombre.length > 40) {
            errors.txtNombre = 'El nombre debe ser de 40 caracteres o menos';
        }  
        
        if (!values.txtCorreo) {
            errors.txtCorreo = 'Por favor, ingresa tu correo';
        } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.txtCorreo)) {
            errors.txtCorreo = 'Correo electrónico invalido';
        }

        if (!values.txtTelefono) {
            errors.txtTelefono = 'Por favor, ingresa tu teléfono';
        } else if (values.txtTelefono.length > 15) {
            errors.txtTelefono = 'El telefono debe ser de 15 digitos o menos';
        }else if(!/^[0-9\b]+$/.test(values.txtTelefono)){
            console.log("FRE")
            errors.txtTelefono = 'El telefono debe ser númerico';            
        }

        if(values.cmbMotivoContacto == ""){ 
            errors.cmbMotivoContacto = 'Elige el motivo de contacto';
        }

        if(!values.taComentarios){
            errors.taComentarios = 'Por favor, ingresa un comentario';
        }
       
        if(Object.keys(errors).length > 0){
            setShowConf(false)
        }

        return errors;
    };     

    //Formik function to set initial values, validate and allow to submit form
    const formik = useFormik({
        initialValues: {
            txtNombre: '',
            txtCorreo: '',
            txtTelefono: '',
            cmbMotivoContacto: '',
            taComentarios: ''          
        },
        validate,       
        onSubmit: (values, {resetForm}) => {
            setShowConf(true)
            resetForm()
        },
      });

      

  return (
    <div>
        <section id="contactContainer" className="contacto">
            <div className="container">
                <div className="container d-flex overflowContactSection">          
                <div className="row">
                    <div className="col-md-6">
                        <div className="row">
                            <div className="col-md-12">
                            <p>
                                <span className="titulo2">¿Tienes preguntas o quieres obtener algún curso? Mándame un correo</span>
                            </p>
                            <div>
                                <span className="contactText">Comprueba mi curso de Educación Financiera, verás un cambio en tu administración y control en tus finanzas. Con mi curso contarás con los conocimientos para crear tu propio negocio.</span>
                                <p className='invitationLegend'>
                                Qué esperas, inscribete ya y comencemos juntos la realización de tu sueño.!!!!!
                                </p>
                            </div>
                            <div>
                                <ul className="contactInfoList">
                                <li><span><i className="fa-solid fa-phone-volume"></i> Teléfono: +52 5587586542</span></li>
                                <li><i className="fa-solid fa-envelope-circle-check"></i> Soporte: support@educacionfinanciera.com</li>
                                </ul>
                            </div>
                            </div>
                        </div>                          
                    </div>

                    <div className="col-md-6 inputContactContainer">                    
                        <form onSubmit={formik.handleSubmit}>
                            <div className="row">
                                <div className="col-md-6">
                                    <label htmlFor="txtNombre" className="form-label contactFormLabel">Nombre</label>
                                    <input
                                        id="txtNombre"
                                        className={formik.errors.txtNombre ? 'formControl form-control invalidFieldForm' :'formControl form-control'}
                                        name="txtNombre"
                                        type="text"
                                        onChange={formik.handleChange}
                                        value={formik.values.txtNombre}
                                    />
                                    {formik.errors.txtNombre ? <div className='invalidFieldValue'>{formik.errors.txtNombre} <i className="fa-solid fa-circle-exclamation"></i></div> : null}                                                                   
                                </div>
                                <div className="col-md-6">
                                    <label htmlFor="txtCorreo" className="form-label contactFormLabel">Correo electrónico</label>
                                    <input
                                        id="txtCorreo"
                                        className={formik.errors.txtNombre ? 'formControl form-control invalidFieldForm' :'formControl form-control'}
                                        name="txtCorreo"
                                        type="text"
                                        onChange={formik.handleChange}
                                        value={formik.values.txtCorreo}
                                    />
                                    {formik.errors.txtCorreo ? <div className='invalidFieldValue'>{formik.errors.txtCorreo} <i className="fa-solid fa-circle-exclamation"></i></div> : null}                                                                    
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6"> 
                                    <label htmlFor="txtTelefono" className="form-label contactFormLabel">Teléfono de contacto</label>
                                    <input
                                        id="txtTelefono"
                                        className={formik.errors.txtNombre ? 'formControl form-control invalidFieldForm' :'formControl form-control'}
                                        name="txtTelefono"
                                        type="text"
                                        onChange={formik.handleChange}
                                        value={formik.values.txtTelefono}
                                    />
                                    {formik.errors.txtTelefono ? <div className='invalidFieldValue'>{formik.errors.txtTelefono} <i className="fa-solid fa-circle-exclamation"></i></div> : null}
                                </div>
                                <div className="col-md-6">
                                    <label htmlFor="cmbMotivoContacto" className="form-label contactFormLabel">Motivo de contacto</label>
                                    <select id="cmbMotivoContacto" className={formik.errors.txtNombre ? 'formControl form-control invalidFieldForm' :'formControl form-control'} name="cmbMotivoContacto" onChange={formik.handleChange} value={formik.values.cmbMotivoContacto}>                                                                                                                
                                        <option value="">Selecciona uina opción</option>
                                        <option value="1">Tengo preguntas, dudas, comentarios</option>
                                        <option value="2">Quiero un curso o plantilla</option>
                                        <option value="3">Necesito soporte</option>
                                    
                                    </select>
                                    {formik.errors.cmbMotivoContacto ? <div className='invalidFieldValue'>{formik.errors.cmbMotivoContacto} <i className="fa-solid fa-circle-exclamation"></i></div> : null}
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-12">
                                    <label htmlFor="taComentarios" className="form-label contactFormLabel">Comentarios</label> 
                                    <textarea id="taComentarios" className={formik.errors.txtNombre ? 'formControl form-control invalidFieldForm' :'formControl form-control'} name="taComentarios" onChange={formik.handleChange} value={formik.values.taComentarios}></textarea>
                                    {formik.errors.taComentarios ? <div className='invalidFieldValue'>{formik.errors.taComentarios} <i className="fa-solid fa-circle-exclamation"></i></div> : null}
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-12 dvBtnContactContainer">
                                    <button type="submit" className='btn btnContacto'>Quiero que me contacten <i className="fa-regular fa-paper-plane  fa-fade"></i></button>
                                </div>        
                            </div>
                            {showConf ? (
                            <div className='row rowSeparationSm'>
                                <div className='col-md-12'>
                                    <Alert variant='success' onClose={() => setShowConf(false)} dismissible>
                                        <Alert.Heading>Datos enviados correctamente!</Alert.Heading>
                                        <p>
                                        Gracias, en breve me pondré en contacto contigo.
                                        </p>
                                    </Alert>
                                </div>
                            </div>      
                            ):(
                                <></>
                            )} 
                        </form>
                    </div>                                      

                </div>
                </div>
            </div>
            </section>           
    </div>
  );
}

export default Contact;